import React from 'react'

const FrontPageInfo = () => {

  return (
    <div className="frontpage-info">
      <h2>Kisatehtvpankki</h2>
      <p>Kisatehtvpankki on arkisto,
      johon voidaan tallettaa tehtvi.
      Sivustolle voi tallettaa tehtvn,
      sek siihen liittyvt liitetiedostot.
      </p>
      <p>
        Kirjautumattomana kyttjn voit
        koostaa tehtvist kisaa varten
        kokonaisuuksia, sek ladata ne
        PDF-tiedostona itsellesi. Tehtvn lisminen "kisakoriin"
        onnistuu painamalla tehtvss esiintyv koripainiketta,
        ja jo mukaan otetut tehtvt lydt ylreunan koria painamalla.
      </p>
      <p>
        Voit myskin selailla tehtvi, arvostella tehtvi
        ja list uusia tehtvi. Uuden tehtvn lisminen arkistoon
        vaatii ensin yllpitjn hyvksynnn uudelle tehtvlle.
      </p>
    </div>
  )
}

export default FrontPageInfo